<template>
  <!-- 订单详情对话框 -->
  <DetailForm ref="detailFormRef" @refresh="refreshData"/>
  <!-- 使用说明 -->

  <!-- 搜索菜单部分的过渡效果 -->
  <transition name="fade">
    <div class="table-com-search" v-show="showSearch">
      <el-card class="search-card" shadow="never">
        <template #header>
          <div class="search-header">
            <span class="search-title">
              <el-icon><Search /></el-icon>
              订单搜索
            </span>
            <el-button type="text" @click="toggleSearch" class="close-btn">
              <el-icon><Close /></el-icon>
            </el-button>
          </div>
        </template>
        
        <el-form :model="searchForm" label-width="100px" class="search-form">
          <el-row :gutter="20">
            <el-col :span="6">
              <el-form-item label="订单号">
                <el-input 
                  v-model="searchForm.order_no" 
                  placeholder="请输入订单号" 
                  clearable 
                  prefix-icon="Search"
                />
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item label="商户订单号">
                <el-input 
                  v-model="searchForm.merchant_order_no" 
                  placeholder="请输入商户订单号" 
                  clearable 
                  prefix-icon="Search"
                />
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item label="商户ID">
                <el-input 
                  v-model="searchForm.merchant_id" 
                  placeholder="请输入商户ID" 
                  clearable 
                  prefix-icon="User"
                />
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item label="订单状态">
                <el-select 
                  v-model="searchForm.status" 
                  placeholder="请选择订单状态" 
                  clearable
                  style="width: 100%"
                >
                  <el-option label="待支付" :value="1" />
                  <el-option label="支付中" :value="2" />
                  <el-option label="支付成功" :value="3" />
                  <el-option label="支付失败" :value="4" />
                  <el-option label="已退款" :value="5" />
                  <el-option label="已关闭" :value="6" />
                </el-select>
              </el-form-item>
            </el-col>
          </el-row>
          
          <el-row :gutter="20">
            <el-col :span="6">
              <el-form-item label="支付产品">
                <el-input 
                  v-model="searchForm.product_name" 
                  placeholder="请输入产品名称" 
                  clearable 
                  prefix-icon="Goods"
                />
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item label="时间范围">
                <el-date-picker
                  v-model="searchForm.timeRange"
                  type="datetimerange"
                  range-separator="至"
                  start-placeholder="开始时间"
                  end-placeholder="结束时间"
                  format="YYYY-MM-DD HH:mm:ss"
                  value-format="YYYY-MM-DD HH:mm:ss"
                  style="width: 100%"
                />
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item>
                <div class="search-buttons">
                  <el-button type="primary" @click="handleSearch" :loading="searchLoading">
                    <el-icon><Search /></el-icon>
                    搜索
                  </el-button>
                  <el-button @click="handleReset">
                    <el-icon><Refresh /></el-icon>
                    重置
                  </el-button>
                </div>
              </el-form-item>
            </el-col>
          </el-row>
        </el-form>
      </el-card>
    </div>
  </transition>
  <!-- 表头 -->
  <div class="table-header ba-scroll-style">
    <!-- 刷新按钮 -->
    <el-button v-permission="'order:list'" :icon="Refresh" @click="refreshData" color="#40485b"/>
    <el-button v-permission="'order:resend'" type="primary" :disabled="!hasSelectedRows" @click="handleBatchReissue">补单</el-button>
    <el-button v-permission="'order:callback'" type="primary" :disabled="!hasSelectedRows" @click="handleBatchCallback">回调</el-button>

    <!-- 统计数据 -->
    <div class="table-statistics">
      <span class="stat-item stat-highlight">累计订单数: <b class="stat-number">150</b></span>
      <span class="stat-item stat-highlight">订单总金额: <b class="stat-number">￥8000.00</b></span>
      <span class="stat-item stat-success">已付订单数: <b class="stat-number">100</b></span>
      <span class="stat-item stat-success">已付总金额: <b class="stat-number">￥6000.00</b></span>
      <span class="stat-item stat-info">商户总收入: <b class="stat-number">￥5976.00</b></span>
      <span class="stat-item stat-warning">未付订单数: <b class="stat-number">50</b></span>
      <span class="stat-item stat-warning">未付总金额: <b class="stat-number">￥2000.00</b></span>
      <span class="stat-item stat-success">成功率: <b class="stat-number">66.67%</b></span>
    </div>

    <!-- 搜索按钮 -->
    <div class="table-search">
      <el-button-group class="ml-4">
        <el-button :icon="Search" @click="toggleSearch" />
      </el-button-group>
    </div>
  </div>

  <!-- 表格内容 -->
  <div style="width: 100%; overflow-x: auto;" class="table-content">
    <el-table
        :data="data.data"
        border
        stripe
        max-height="600"
        style="width: 100%;"
        @selection-change="handleSelectionChange"
    >
      <el-table-column type="selection" width="40"/>

      <el-table-column prop="order_no" label="平台单号" align="center" min-width="200"/>
      <el-table-column prop="merchant_order_no" label="商户订单号" align="center" min-width="167"/>
      <el-table-column prop="merchant.merchant_name" label="商户名称" align="center" min-width="150"/>
      <el-table-column prop="amount" label="金额" align="center" min-width="80">
        <template #default="{ row }">
          {{ (row.amount / 100).toFixed(2) }}
        </template>
      </el-table-column>

      <!-- 订单状态分组 -->
      <el-table-column label="订单状态" align="center" min-width="200">
        <el-table-column label="状态" width="100" align="center">
          <template #default="{ row }">
            <el-tag :type="getStatusTagType(row.status) as any">
              {{ getStatusText(row.status) }}
            </el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="paid_time" label="支付时间" width="100" align="center"/>
      </el-table-column>

      <el-table-column prop="product.product_name" label="支付产品" align="center" min-width="120"/>
      <el-table-column prop="channel.channel_name" label="支付通道" align="center" min-width="120"/>
      <el-table-column prop="fee" label="费用" align="center" min-width="80">
        <template #default="{ row }">
          {{ (row.fee / 100).toFixed(2) }}
        </template>
      </el-table-column>

      <el-table-column prop="notify_status" label="通知状态" align="center" min-width="100">
        <template #default="{ row }">
          <el-tag :type="getNotifyStatusTagType(row.notify_status) as any" size="small">
            {{ getNotifyStatusText(row.notify_status) }}
          </el-tag>
        </template>
      </el-table-column>

      <el-table-column prop="created_at" label="创建时间" align="center" min-width="180"/>

      <el-table-column label="操作" align="center" fixed="right" min-width="250">
        <template #default="scope">
          <el-button v-permission="'order:detail'" size="small" @click="openDetailFrom(scope.row)">订单详情</el-button>
          <el-button v-permission="'order:query'" size="small" type="info" @click="handleQuery(scope.row)">查单</el-button>
          <el-button v-permission="'order:refund'" size="small" type="warning" @click="handleRefund(scope.row)" v-if="scope.row.status === 3">退款</el-button>
          <el-button v-permission="'order:resend'" size="small" type="primary" @click="handleReissue(scope.row)" v-if="[4, 6].includes(scope.row.status)">补单</el-button>
          <el-button v-permission="'order:callback'" size="small" type="success" @click="handleCallback(scope.row)" v-if="scope.row.status === 3">回调</el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
  <div class="table-footer">
    <div class="table-pagination">
      <el-pagination
          v-model:current-page="data.current_page"
          v-model:page-size="data.page_size"
          :page-sizes="[10, 50, 100, 200]"
          :background="true"
          prev-text="上一页"
          next-text="下一页"
          layout="total, sizes, prev, pager, next, jumper"
          :total="data.page_total"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
      />
    </div>
  </div>

</template>

<style scoped>
.search-card {
  margin-bottom: 20px;
  border: 1px solid #e4e7ed;
  border-radius: 8px;
}

