  border-radius: 8px;
  margin-bottom: 20px;
}
</style>

<script lang="ts" setup>
import { Refresh, Search, Close } from '@element-plus/icons-vue';
import { ref, onMounted, defineAsyncComponent, nextTick } from 'vue';
import { ElMessage, ElMessageBox } from 'element-plus';
import request from '@/utils/request';

// 引入自定义插件和 API
import { useTableDataPlugin } from "@/utils/plugins/table.plugin.ts";

// 导入详情页面
const DetailForm = defineAsyncComponent(() => import('./detail.vue'));
// 导入详情页面
const detailFormRef = ref(null);
const openDetailFrom = (row = {}) => {
  console.log(row);
  // 使用 nextTick 确保组件已经挂载
  nextTick(() => {
    if (detailFormRef.value) {
      detailFormRef.value.open({ ...row}); // 传递 `row` 作为参数
    } else {
      console.error("DetailForm 组件未正确挂载！");
    }
  });
};
//
// // 使用表格插件来管理表格的状态、数据等
// const {
//   handleSelectionChange,
//   hasSelectedRows,
//   isVisible: showSearch,
//   toggleVisibility: toggleSearch,
//   loadData,
//   data,
//   refreshData,
//   handleSizeChange,
//   handleCurrentChange,
//   searchParams,
//   resetSearchParams
// } = useTableDataPlugin("/api/v1/admin/order");
const {
  handleSelectionChange,
  hasSelectedRows,
  isVisible: showSearch,
  toggleVisibility: toggleSearch,
  loadData,
  data,
  refreshData,
  handleSizeChange,
  handleCurrentChange,
  searchParams
} = useTableDataPlugin("/api/v1/admin/order");
// 确保搜索参数有正确的结构
if (!(searchParams.value as any).search) {
  (searchParams.value as any).search = {};
}

// 初始化搜索参数
const initSearchParams = () => {
  if (!(searchParams.value as any).search) {
    (searchParams.value as any).search = {
      order_no: '',
      merchant_order_no: '',
      merchant_id: '',
      status: '',
      product_name: '',
      start_time: '',
      end_time: ''
    };
  }
};

// 搜索表单数据
const searchForm = ref({
  order_no: '',
  merchant_order_no: '',
  merchant_id: '',
  status: '',
  product_name: '',
  timeRange: null as [Date, Date] | null
});


// 搜索加载状态
const searchLoading = ref(false);


// 处理搜索
const handleSearch = async () => {
  searchLoading.value = true;
  try {
    // 处理时间范围
    let searchData: any = { ...searchForm.value };
    if (searchData.timeRange && searchData.timeRange.length === 2) {
      searchData.start_time = searchData.timeRange[0];
      searchData.end_time = searchData.timeRange[1];
    }
    delete searchData.timeRange;
    delete searchData.quickTime;
    
    // 将搜索表单数据同步到searchParams
    (searchParams.value as any).search = searchData;
    await refreshData();
  } catch (error) {
    console.error('搜索失败:', error);
    ElMessage.error('搜索失败，请重试');
  } finally {
    searchLoading.value = false;
  }
};

// 处理重置
const handleReset = () => {
  searchForm.value = {
    order_no: '',
    merchant_order_no: '',
    merchant_id: '',
    status: '',
    product_name: '',
    timeRange: null
  };
  (searchParams.value as any).search = { ...searchForm.value };
  refreshData();
};

// 在组件挂载时初始化搜索参数和加载数据
onMounted(() => {
  initSearchParams();
  loadData();
});

// 获取订单状态文本
const getStatusText = (status: number) => {
  const statusMap: { [key: number]: string } = {
    1: '待支付',
    2: '支付中',
    3: '支付成功',
    4: '支付失败',
    5: '已退款',
    6: '已关闭'
  };
  return statusMap[status] || '未知状态';
};

// 获取订单状态标签类型
const getStatusTagType = (status: number) => {
  const typeMap: { [key: number]: string } = {
    1: 'warning',
    2: 'primary',
    3: 'success',
    4: 'danger',
    5: 'info',
    6: 'info'
  };
  return typeMap[status] || 'info';
};

// 获取通知状态文本
const getNotifyStatusText = (status: number) => {
  const statusMap: { [key: number]: string } = {
    0: '未通知',
    1: '通知成功',
    2: '通知失败'
  };
  return statusMap[status] || '未知状态';
};

// 获取通知状态标签类型
const getNotifyStatusTagType = (status: number) => {
  const typeMap: { [key: number]: string } = {
    0: 'warning',
    1: 'success',
    2: 'danger'
  };
  return typeMap[status] || 'info';
};

// 处理退款
const handleRefund = async (row: any) => {
  try {
    await ElMessageBox.confirm(
      `确定要退款订单 ${row.order_no} 吗？`,
      '确认退款',
      {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning',
      }
    );

    // 调用退款API
    const { refundOrder } = await import('@/api/orderManagement.ts');
    const res = await refundOrder('/api/v1/admin/order/refund', {
      order_id: row.id,
      reason: '管理员退款'
    });

    if (res.status) {
      ElMessage.success('退款申请已提交');
      refreshData();
    } else {
      ElMessage.error(res.message);
    }
  } catch (error) {
    if (error !== 'cancel') {
      ElMessage.error('退款申请失败');
    }
  }
};

// 处理补单
const handleReissue = async (row: any) => {
  try {
    await ElMessageBox.confirm(
      `确定要补单 ${row.order_no} 吗？`,
      '确认补单',
      {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning',
      }
    );
    
    // 调用补单API
    const result = await request.post('/api/v1/admin/order/reissue', {
      ids: [row.id]
    });
    
    if ((result as any).code === 200) {
      ElMessage.success('补单成功');
      refreshData();
    } else {
      ElMessage.error((result as any).msg);
    }
  } catch (error) {
    ElMessage.error('补单失败');
  }
};

// 处理回调
const handleCallback = async (row: any) => {
  try {
    await ElMessageBox.confirm(
      `确定要重新回调 ${row.order_no} 吗？`,
      '确认回调',
      {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning',
      }
    );
    
    // 调用回调API
    const result = await request.post('/api/v1/admin/order/callback', {
      ids: [row.id]
    });
    
    if ((result as any).code === 200) {
      ElMessage.success('回调成功');
      refreshData();
    } else {
      ElMessage.error((result as any).msg);
    }
  } catch (error) {
    ElMessage.error('回调失败');
  }
};

// 批量补单
const handleBatchReissue = async () => {
  if (!hasSelectedRows.value) {
    ElMessage.warning('请选择要补单的订单');
    return;
  }
  
  try {
    await ElMessageBox.confirm(
      `确定要批量补单选中的订单吗？`,
      '确认批量补单',
      {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning',
      }
    );
    
    // 调用补单API（统一使用ids参数）
    const result = await request.post('/api/v1/admin/order/reissue', {
      ids: (data.value as any).selectedRows?.map((row: any) => row.id) || []
    });
    
    if ((result as any).code === 200) {
      ElMessage.success('批量补单成功');
      refreshData();
    } else {
      ElMessage.error((result as any).msg);
    }
  } catch (error) {
    ElMessage.error('批量补单失败');
  }
};

// 批量回调
const handleBatchCallback = async () => {
  if (!hasSelectedRows.value) {
    ElMessage.warning('请选择要回调的订单');
    return;
  }
  
  try {
    await ElMessageBox.confirm(
      `确定要批量回调选中的订单吗？`,
      '确认批量回调',
      {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning',
      }
    );
    
    // 调用回调API（统一使用ids参数）
    const result = await request.post('/api/v1/admin/order/callback', {
      ids: (data.value as any).selectedRows?.map((row: any) => row.id) || []
    });
    
    if ((result as any).code === 200) {
      ElMessage.success('批量回调成功');
      refreshData();
    } else {
      ElMessage.error((result as any).msg);
    }
  } catch (error) {
    ElMessage.error('批量回调失败');
  }
};

// 处理查单
const handleQuery = async (row: any) => {
  try {
    // 调用查单API
    const result = await request.post('/api/v1/admin/order/query', {
      order_no: row.order_no
    });
    
    if ((result as any).code === 200) {
      const queryData = (result as any).data;
      
      // 显示查单结果
      let message = `📋 订单信息\n`;
      message += `═══════════════════════════════════════════════════════════\n`;
      
      // 订单基本信息
      message += `🔢 订单号：${queryData.order_no}\n`;
      message += `📊 当前状态：${getStatusText(queryData.status)}\n`;
      message += `💰 订单金额：¥${(queryData.amount / 100).toFixed(2)}\n`;
      message += `📅 创建时间：${new Date(queryData.created_at).toLocaleString()}\n\n`;
      
      // 支付通道查询结果
      message += `🔍 支付通道查询结果\n`;
      message += `───────────────────────────────────────────────────────────\n`;
      
      if (queryData.query_success) {
        message += `✅ 查询状态：成功\n`;
        
        // 显示当前状态（优先显示统一状态，否则显示通道消息）
        if (queryData.query_result && queryData.query_result.unified_status_text) {
          message += `📊 当前状态：${queryData.query_result.unified_status_text}\n`;
        } else {
          message += `📊 当前状态：${queryData.query_message}\n`;
        }
        
        // 显示统一状态信息
        if (queryData.query_result && queryData.query_result.unified_status_text) {
          message += `\n📋 状态详情：\n`;
          message += `───────────────────────────────────────────────────────────\n`;
          message += `🎯 统一状态：${queryData.query_result.unified_status_text}\n`;
          message += `📝 原始状态：${queryData.query_result.original_status || '未知'}\n`;
          message += `📊 状态字段：${queryData.query_result.original_status_field || 'status'}\n`;
          message += `🏢 供货商类型：${queryData.query_result.supplier_type || '未知'}\n`;
        }
        
        if (queryData.query_result && Object.keys(queryData.query_result).length > 0) {
          // message += `\n📄 通道返回数据：\n`;
          // message += `───────────────────────────────────────────────────────────\n`;
          //
          // // 格式化JSON数据，确保完整显示
          // const jsonString = JSON.stringify(queryData.query_result, null, 2);
          // // 如果JSON数据太长，进行适当的分段显示
          // if (jsonString.length > 2000) {
          //   message += `数据较大，共 ${jsonString.length} 字符\n`;
          //   message += `前1000字符：\n${jsonString.substring(0, 1000)}...\n`;
          //   message += `\n后1000字符：\n...${jsonString.substring(jsonString.length - 1000)}\n`;
          //   message += `\n完整数据已输出到控制台，请按F12查看\n`;
          //   console.log('完整查单结果数据：', queryData.query_result);
          // } else {
          //   message += `${jsonString}\n`;
          // }
        } else {
          message += `\n📄 通道返回数据：无数据\n`;
        }
      } else {
        message += `❌ 查询状态：失败\n`;
        message += `⚠️  错误信息：${queryData.query_message}\n`;
      }
      
      message += `\n═══════════════════════════════════════════════════════════`;
      
      // 使用HTML格式显示，支持更好的样式控制
      const htmlMessage = message.replace(/\n/g, '<br>').replace(/`([^`]+)`/g, '<code>$1</code>');
      
      ElMessageBox.alert(htmlMessage, '🔍 查单结果', {
        confirmButtonText: '确定',
        type: queryData.query_success ? 'success' : 'warning',
        dangerouslyUseHTMLString: true,
        customStyle: {
          'max-width': '900px',
          'max-height': '700px',
          'overflow': 'auto',
          'font-family': 'monospace',
          'line-height': '1.6',
          'white-space': 'pre-wrap',
          'word-break': 'break-all'
        }
      });
    } else {
      console.error('查单API返回错误：', result);
      ElMessage.error((result as any).msg || '查单失败');
    }
  } catch (error) {
    console.error('查单请求异常：', error);
    ElMessage.error('查单失败：' + ((error as any).message || '网络错误'));
  }
};

